<template>
  <el-tabs type="border-card" v-model="activeTab" :before-leave="leaveTab" @tab-click="modelChange" :stretch=true>
    <el-tab-pane
      label=""
      name="one"
      :disabled="tabOneCannotSelect">
      <div class="upload-box">
        <input type="file" ref="getfile" class="choose" @change="getFile($event)" multiple="multiple">
        <div class="to-choose" ref="dropBox" @click="toGetFile" @drop="dropFile">
          <span></span>
          <span class="note">zip</span>
          <div class="choose-progress"></div>
        </div>
        <div class="chosen-files">
          <div class="header">
            <span class="name"></span>
            <span class="type"></span>
            <span class="size"></span>
          </div>
          <div class="file-info" v-for="(item, index) in fileList" :key="item.name">
            <span class="file-name">{{ item.name }}</span>
            <span class="file-type">{{ item.type | typeFormat }}</span>
            <span class="file-size">{{ item.size | sizeFormat }}</span>
            <div class="delete" @click="deleteData(index)">
              <i class="el-icon-delete"></i>
            </div>
          </div>
        </div>
      </div>
    </el-tab-pane>
    <el-tab-pane
      label=""
      name="two"
      :disabled="tabTwoCannotSelect">
      <el-tree
        v-loading="loading"
        element-loading-text="..."
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        ref="tree"
        :props="props"
        :load="loadNode"
        node-key="storagePath"
        default-expand-all
        lazy
        show-checkbox
        :default-expanded-keys="checkedKeyList"
        :highlight-current="true"
        @node-click="handleNodeClick"
        @check-change="getUserCheckedNodes">
      </el-tree>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
export default {
  filters: {
    //
    typeFormat(msg){
      if(msg.search('zip') !== -1){
        return 'zip'
      } else if(msg.search('jpg') !== -1 || msg.search('jpeg') !== -1){
        return 'jpg'
      } else if(msg.search('png') !== -1){
        return 'png'
      } else {
        return ''
      }
    },
    //
    sizeFormat(msg){
      if(msg < 1000000){
        return (msg/1000).toFixed(1) + 'KB'
      } else if(msg > 1000000){
        return (msg/1000000).toFixed(1) + 'MB'
      }
    }
  },
  data () {
    return{
      fileList: [], //
      fileList02: [], //
      checkedKeyList: [], //key
      //
      activeTab: 'one',
      //
      tabOneCannotSelect: false,
      tabTwoCannotSelect: false,
      //
      url: 'v1/server/share?directory=/',
      //treeDataelementUInamelabel,childrenchildren
      //isLeaftruefalse
      props: {
        label: 'name',
        children: 'children',
        isLeaf: 'leaf'
      },
      //
      loading: true,
    }
  },
  created() {
    //
    document.addEventListener('drop', function (e) {
      e.preventDefault()
    }, false)
    document.addEventListener('dragover', function (e) {
      e.preventDefault()
    }, false)
    //
    this.displayByUserChoiceModel()
    //store
    this.getFileData()
    this.getFileData02()
    this.getTreeCheckedKey()
    this.setTreeCheckedByKey()
  },
  methods: {
    //input:file
    toGetFile(){
      this.$refs.getfile.dispatchEvent(new MouseEvent('click'))
    },
    //
    /** */
    //
    getFile(event){
      this.fileList.push(...event.target.files)
      console.log(this.fileList);
      this.$store.commit('saveFileList', this.fileList)
    },
    //
    dropFile(e){
      e.preventDefault()
      let files = [];
      [].forEach.call(e.dataTransfer.files, function(file) {
        files.push(file)
      },false)
      this.fileList.push(...files)
      console.log(this.fileList)
      this.$store.commit('saveFileList', this.fileList)
    },
    //store
    getFileData(){
      this.fileList = this.$store.state.allFileList
    },
    //store
    getFileData02(){
      this.fileList02 = this.$store.state.allFileList02
    },
    //storekey
    getTreeCheckedKey(){
      this.checkedKeyList = this.$store.state.treeCheckedKeyList
    },
    //key
    setTreeCheckedByKey(){
      //
      this.$nextTick(()=>{
        this.$refs.tree.setCheckedKeys(this.checkedKeyList)
      })
      //
      this.closeLoading()
    },
    //
    deleteData:function(index){
      this.$delete(this.fileList, index);
      this.$store.commit('saveFileList', this.fileList)
    },
    //
    displayByUserChoiceModel(){
      if (this.$store.state.userChoiceModel === 1){
          this.activeTab = 'one'
      }else if (this.$store.state.userChoiceModel === 2){
          this.activeTab = 'two'
      }
    },
    //
    //tab
    leaveTab(activeName, oldActiveName){
        if (this.fileList.length !== 0){
            this.tabTwoCannotSelect = true;
            let p = new Promise((resolve, reject) => {
                this.$confirm('', '', {
                    showConfirmButton: false,
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => {
                    this.tabTwoCannotSelect = false;
                    reject(err)
                })
            })
            return p
        }
        if (this.fileList02.length !== 0){
            this.tabOneCannotSelect = true;
            let p = new Promise((resolve, reject) => {
                this.$confirm('', '', {
                    showConfirmButton: false,
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => {
                    this.tabOneCannotSelect = false;
                    reject(err)
                })
            })
            return p
        }
    },

    modelChange(tab,event){
      if (tab.active) { //
        if (tab.name === 'one'){//indexuserChoiceModel1
          this.$store.commit('changeUserChoiceModel',1)
        }else if (tab.name === 'two'){//indexuserChoiceModel2
          this.$store.commit('changeUserChoiceModel',2)
        }
      }
    },

    //
    //
    getUserCheckedNodes(){
      //
      this.fileList02 = this.$refs.tree.getCheckedNodes(); //getCheckedNodes()
      console.log(this.fileList02)
      this.$store.commit('saveFileList02', this.fileList02)
      //node-keytreeId
      this.checkedKeyList = this.$refs.tree.getCheckedKeys(); //getCheckedKeys()
      console.log(this.checkedKeyList)
      this.$store.commit('saveTreeCheckedKeyList', this.checkedKeyList)
    },
    //
    closeLoading(){
      setTimeout(()=>{
        this.loading = false
        // console.log("")
      },3000) //3
    },
    //
    handleNodeClick(data, checked, indeterminate) {
      console.log(data);
    },
    //
    loadNode(node, resolve) {
      if (node.level === 0) {
        this.$http.get(this.url).then((res)=>{
          let treeNode = [];
          if (res.data.length === 0){
              node.data.disabled=true;
              console.log(node.data.name + "");
          }
          else {
            for (let i = 0; i < res.data.length; i++) {
              let obj;
              if (res.data[i].type === 'DIR') {
                obj = {
                    name: res.data[i].name,
                    type: res.data[i].type,
                    storagePath: '/' + res.data[i].name + '/',
                    nextUrl: this.url + res.data[i].name + '/',
                    leaf: false,
                    children: []
                };
              } else {
                obj = {
                    name: res.data[i].name,
                    type: res.data[i].type,
                    storagePath: '/' + res.data[i].name,
                    nextUrl: this.url + res.data[i].name + '/',
                    leaf: true,
                    children: []
                };
              }
              treeNode.push(obj);
            }
          }
          return resolve(treeNode);
        }).catch((res)=>{
            console.log(this.url + '');
            resolve([]);
        });
      }

      if (node.data){
        let hasChild;
        if (node.data.type === 'DIR') {
            hasChild = true;
        } else{
            hasChild = false;
        }
        let tPath = node.data.storagePath;
        let tUrl = node.data.nextUrl;
        setTimeout(() => {
          if (hasChild) {
            this.$http.get(tUrl).then((res)=>{
              let treeNode = [];
              if (res.data.length === 0){
                node.data.disabled=true;
                console.log(node.data.name + "");
              }
              else {
                for(let i = 0; i<res.data.length;i++) {
                  let obj;
                  if (res.data[i].type === 'DIR'){
                    obj = {
                      name: res.data[i].name,
                      type: res.data[i].type,
                      storagePath: tPath + res.data[i].name + '/',
                      nextUrl: tUrl + res.data[i].name + '/',
                      leaf: false,
                      children: []
                    };
                  }else {
                    obj = {
                      name: res.data[i].name,
                      type: res.data[i].type,
                      storagePath: tPath + res.data[i].name,
                      nextUrl: tUrl + res.data[i].name + '/',
                      leaf: true,
                      children: []
                    };
                  }
                  treeNode.push(obj);
                }
              }
              return resolve(treeNode);
            }).catch((res)=>{
              console.log(tUrl + '');
              resolve([]);
            });
          } else {
              // console.log(node.data.name + "")
              resolve([]);
          }
        }, 400);
      } else{
        // console.log('node.data')
      }
    },
  },
}
</script>
<style lang="less" scoped>
.upload-box{
  width: 100%;
  height: 460px;
  position: relative;
  .choose{
    display: none;
  }
  .to-choose{
    position: absolute;
    left: 0;
    width: 380px;
    height: 100%;
    border: 2px dashed #aceedb;
    box-sizing: border-box;
    border-radius: 30px;
    background-color: #ecf5f3;
    text-align: center;
    line-height: 440px;
    font-size: 28px;
    color: #7c8786;
    .choose-progress{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 30px;
      background-color: rgba(172,238,219,0.5);
    }
    .note{
      width: 100%;
      height: 30px;
      position: absolute;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
      line-height: 30px;
      font-size: 14px;
    }
  }
  .to-choose:hover{
    border: 2px dashed #6fcdb2;
  }
  .chosen-files{
    position: absolute;
    border: 2px solid #aceedb;
    background-color: #ecf5f3;
    box-sizing: border-box;
    border-radius: 20px;
    height: 100%;
    width: 480px;
    right: 0;
    overflow: auto;
    .header{
      width: 100%;
      height: 30px;
      background-color: rgba(0,0,0,0.1);
      span{
        float: left;
        text-align: center;
        line-height: 30px;
      }
      .name{
        width: 216px;
      }
      .type{
        width: 105px;
      }
      .size{
        width: 105px;
      }
    }
    .file-info{
      height: 30px;
      width: 100%;
      transition: 0.2s;
      span{
        height: 30px;
        float: left;
        text-align: center;
        line-height: 30px;
        overflow: hidden;
      }
      .file-name{
        width: 216px;
        font-size: 14px;
      }
      .file-type{
        width: 105px;
      }
      .file-size{
        width: 105px;
      }
      .delete{
        float: left;
        height: 30px;
        width: 30px;
        color: rgba(0,0,0,0.3);
        transition: color 0.2s;
        text-align: center;
        line-height: 30px;
      }
      .delete:hover{
        color: rgba(0,0,0,0.9);
      }
    }
    .file-info:hover{
      background-color: rgba(0,0,0,0.1);
    }
  }
}
</style>
