<template>
  <div>
    <div class="userInformation">
      <ul>
        <li><i class="el-icon-user">&nbsp;</i>{{ userInfo.username }}</li>
        <li><i class="el-icon-message">&nbsp;</i>{{ userInfo.email }}</li>
        <li><i class="el-icon-date">&nbsp;</i>{{ userInfo.date_joined|timefilters }}</li>
      </ul>
    </div>
    <div class="logout"  @click="logout">
      <span></span>
    </div>
    <div>
      <!--  -->
      <el-button id="BtnChangePwd" type="text" @click="dialogFormVisible = true" ></el-button>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
        <!---->
        <el-form :model="changePwdForm" status-icon :rules="rules" ref="changePwdForm" label-width="100px" class="demo-changePwdForm">
          <el-form-item label="" prop="old_password">
            <el-input type="password" v-model="changePwdForm.old_password" autocomplete="off" placeholder="" show-password></el-input>
          </el-form-item>
          <el-form-item label="" prop="new_password1">
            <el-input type="password" v-model="changePwdForm.new_password1" autocomplete="off" placeholder="" show-password></el-input>
          </el-form-item>
          <el-form-item label="" prop="new_password2">
            <el-input type="password" v-model="changePwdForm.new_password2" autocomplete="off" placeholder="" show-password></el-input>
          </el-form-item>
          <!---->
          <el-form-item>
            <el-button @click="dialogFormVisible = false,resetForm('changePwdForm')"> </el-button>
            <el-button type="primary" @click="dialogFormVisible = false,submitForm('changePwdForm')"> </el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  filters: {
    //UTC
    timefilters(val) {
      if (val == null || val == "") {
        return "";
      } else {
        let d = new Date(val);   //val 
        let month =
            d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
        let day = d.getDate() < 10 ? "0" + d.getDate() : d.getDate();
        let hours = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
        let min = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();
        let sec = d.getSeconds() < 10 ? "0" + d.getSeconds() : d.getSeconds();
        let times=d.getFullYear() + '-' + month + '-' + day + ' ' + hours + ':' + min + ':' + sec;
        return times;
      }
    }
  },
  data(){
    //
    let validateOldPass = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }else{
        callback();
      }
    };
    //
    let validateNewPass1 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.changePwdForm.new_password2 !== '') {
          this.$refs.changePwdForm.validateField('new_password2');
        }
        callback();
      }
    };
    //
    let validateNewPass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.changePwdForm.new_password1) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return{
      //
      userInfo:{},
      //dialog
      dialogFormVisible: false,
      //
      changePwdForm: {
        old_password: '',
        new_password1: '',
        new_password2: '',
      },
      //
      rules: {
        old_password: [
          { validator: validateOldPass, trigger: 'blur' }
        ],
        new_password1: [
          { validator: validateNewPass1, trigger: 'blur' }
        ],
        new_password2: [
          { validator: validateNewPass2, trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getUserInfo()
  },
  methods: {
    //
    getUserInfo(){
      this.$http.get('v1/users/self').then((res)=>{
        this.userInfo = res.data;
        console.log(this.userInfo);
      })
    },
    //
    logout(){
      this.$http.post('v1/auth/logout').then((e)=>{
        console.log(e);
        window.sessionStorage.removeItem('token')
        this.$message({
          message:"",
          type: "success"
        })
        this.$router.push('/login')
      })
    },
    //
    submitForm(formName) {
      console.log(this.changePwdForm)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert('');
          this.$http.post('v1/auth/password/change', {old_password: this.changePwdForm.old_password,
            new_password1: this.changePwdForm.new_password1,new_password2: this.changePwdForm.new_password2}).then(e => {
            console.log(e)
            this.$message({
              message: '! ' + e.data.error,
              type: "error"
            })
          }).catch((e)=>{
            console.log(e);
            this.$message({
              message: e,
              type: "error"
            })
          })
        } else {
            console.log('');
            this.$message({
              message: "",
              type: "error"
            })
            return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
}
</script>

<style lang="less" scoped>
.userInformation{
  width: 400px;
  border-radius: 8px;
  background-color: #ffffff;
  li{
    padding: 10px;
    list-style:none;
  }
}

.logout {
  height: 40px;
  width: 100px;
  margin-top: 50px;
  border-radius: 5px;
  background-color: #e8fff0;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
}
  .logout:hover{
    background-color: #c0ffd9;
  }

#BtnChangePwd{
  height: 40px;
  width: 100px;
  margin-top: 50px;
  border-radius: 5px;
  background-color: #e8fff0;
  text-align: center;
  size: landscape;
  color: #000000;
  font-size: medium;
  cursor: pointer;
}
#BtnChangePwd:hover{
   background-color: #c0ffd9;
 }


</style>
